.backButton {
  position: sticky;
  top: 1em;
  left: 1em;
  width: 100px;
  height: 50px;
  display: flex;
  justify-content: center;
  background-color: white;
  transition: background .2s ease-in-out;

  &:hover{
    background-color: #232629;
    cursor: pointer;
  }

  a {
    text-align: center;
    display: block;
    text-decoration: none;
    color: black;
    transition: color .1s ease-in-out;

    &::before{
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: white solid 2px;
      transition: top, left, right, bottom .1s ease-in-out, border .15s ease-in-out;
    }

    &:hover{
        color: white;

        &::before{
          border: #ff5857 solid 2px;
          top: -4px;
          left: -4px;
          right: -4px;
          bottom: -4px;
        }
    }
  }
}

