.backButton {
  position: -webkit-sticky;
  position: sticky;
  top: 1em;
  left: 1em;
  width: 100px;
  height: 50px;
  display: flex;
  justify-content: center;
  background-color: white;
  transition: background 0.2s ease-in-out;
}
.backButton:hover {
  background-color: #232629;
  cursor: pointer;
}
.backButton a {
  text-align: center;
  display: block;
  text-decoration: none;
  color: black;
  transition: color 0.1s ease-in-out;
}
.backButton a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: white solid 2px;
  transition: top, left, right, bottom 0.1s ease-in-out, border 0.15s ease-in-out;
}
.backButton a:hover {
  color: white;
}
.backButton a:hover::before {
  border: #ff5857 solid 2px;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
}

.contentWrapper {
  margin: 1em auto 0 auto;
  width: 60%;
  padding: 1em;
  border-radius: 1em;
}
.contentWrapper .titleText h1 {
  font-size: 5em;
  text-align: center;
  padding-bottom: 1em;
  border-bottom: rgba(255, 255, 255, 0.475) solid 1px;
}
.contentWrapper .content {
  font-size: x-large;
  padding-top: 1em;
  margin-top: 1em;
  min-height: 512px;
  border-radius: 1em;
  color: white;
}/*# sourceMappingURL=Entry.css.map */